import React from "react"
import styled from "@emotion/styled"
import { graphql, useStaticQuery } from "gatsby"
import Image from "gatsby-image"

const HeroContainer = styled.section`
  width: 100%;
  max-width: 1300px;
  padding: 25px 0;
  text-align: center;
  .image {
    display: none;
  }
  .information {
    h2 {
      font-size: 2.8rem;
      margin: 0 0 10px 0;
    }
    h3,
    p {
      margin: 0 0 5px 0;
      font-size: 2.4rem;
    }
  }
  @media (min-width: 500px) {
    display: flex;
    justify-content: space-around;
    padding: 25px;
    .image {
      display: block;
      width: 40%;
      height: 200px;
    }
    .information {
      width: 60%;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  @media (min-width: 730px) {
    .image {
      height: 30vh;
    }
  }
  @media (min-width: 1023px) {
    .image {
      height: 50vh;
    }
    .information {
      h2,
      h3,
      p {
        font-size: 3rem;
      }
    }
  }
`

const Hero = () => {
  const {
    data: { nodes },
  } = useStaticQuery(graphql`
    query {
      data: allFile(
        filter: {
          sourceInstanceName: { eq: "images" }
          relativePath: { eq: "Erickowski.jpeg" }
        }
      ) {
        nodes {
          relativePath
          sharp: childImageSharp {
            fluid(maxWidth: 400) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  `)
  const {
    sharp: { fluid },
  } = nodes[0]
  return (
    <HeroContainer>
      <Image fluid={fluid} className="image" />
      <div className="information">
        <h2>Sobre mí</h2>
        <h3>Erick Mata</h3>
        <p>Frontend UI Developer</p>
      </div>
    </HeroContainer>
  )
}

export default Hero
